@keyframes colorCycle {
  0% {
    color: #7fd5e0;
  }
  8.3333333333% {
    color: #f5ce6d;
  }
  16.6666666667% {
    color: #ba91ed;
  }
  25% {
    color: #dd2e2e;
  }
  33.3333333333% {
    color: #9ab9f2;
  }
  41.6666666667% {
    color: rgb(4, 32, 27);
  }
  50% {
    color: #e49afc;
  }
  58.3333333333% {
    color: #aeb4b2;
  }
  66.6666666667% {
    color: #af7039;
  }
  75% {
    color: #2536a4;
  }
  83.3333333333% {
    color: rgb(192, 184, 170);
  }
  91.6666666667% {
    color: #7fd5e0;
  }
}
