.condensed-title {
  font-size: 0.7rem;
  height: 2rem;
  display: flex;
  margin: auto;
  padding-bottom: 0.5rem;
  justify-content: center;
  width: 200px;
}
@media (min-width: 370px) {
  .condensed-title {
    height: 1rem;
    width: 370px;
  }
}
@media (min-width: 470px) {
  .condensed-title {
    width: 470px;
  }
}
@media (min-width: 570px) {
  .condensed-title {
    margin: auto;
    font-size: 1rem;
    height: 1rem;
    width: 570px;
  }
}

.condensed-container {
  border: 7px solid rgb(237, 236, 232);
  border-radius: 19px;
  background-color: #fff;
  padding: 0.25rem;
  display: block;
  flex-direction: column;
  align-items: center;
  width: fit-content;
  margin: auto;
}
@media (min-width: 470px) {
  .condensed-container {
    border: 7px solid rgb(237, 236, 232);
  }
}
@media (min-width: 570px) {
  .condensed-container {
    padding: 1rem;
    flex-flow: row;
    align-items: flex-start;
    justify-content: center;
  }
}

.condensed-tracks {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
@media (min-width: 570px) {
  .condensed-tracks {
    flex-flow: row;
    align-items: flex-start;
  }
}

.condensed-direction-up {
  align-items: flex-start;
}
@media (min-width: 570px) {
  .condensed-direction-up {
    align-items: flex-end;
  }
}

.condensed-vertical-track-container {
