// font for community-icon
@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@500&display=swap');


html {
    font-size: 16px;
    // margin: auto;
    transition: all 500ms ease;
    box-sizing: border-box;
    background-color: $background-color;
    overflow-x: hidden;

}

body {
    font-family: -apple-system, "system-ui", "Segoe UI", Roboto, "Helvetica Neue", Arial, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", sans-serif;
    font-family: $lead-font;
    // padding-left: 1rem;
    max-width: 100vw;
    overflow-x: hidden;
    padding-bottom: 1rem;
}

ul {
    list-style-type: none;
    /* Removes bullets from an unordered list */
    padding: 0;

}

// in root.js
