}
p{
    color: $lead-text-color;
}

// in root.js
.main-container {
    display: flex;
    flex-flow: column;
    padding-top: 1rem;
    overflow: hidden;
    justify-content: center;
}

.horizontal-chronology {
    display: flex;
    flex-flow: row;
}




